






















































@import '../scss/variable';

ul {
  list-style-type: none;
  padding: 0px;
  margin-top: 0px;
  text-align: left;
}
li {
  display: flex;
  height: 40px;
  line-height: 40px;
  background: $color1;
  margin: 0.6rem 0;
  border-radius: 6px;
  color: $white;
  &:hover{
    margin-left: 7px;
    opacity: 0.8;
  }
  >div{
    display: flex;
    width: 100%;
    flex-direction: row;
    align-items: center;
  }
  &.edit_li{
    background: #fff;
    opacity: 1;
  }
  &.todoComplete_li {
    background:rgb(201, 201, 201)
  }
}
.btn_complete{
  z-index: 1;
  height: 40px;
  width: 3.5rem;
  font-size: 1.2rem;
  border-radius: 6px 0 0 6px;
  color: rgb(176, 0, 82);
  font-weight: bold;
  text-shadow: 1px 1px 1px rgb(50, 34, 0);
  cursor: pointer;
  &:hover{
    background: rgb(176, 0, 82);
    color: white;
  }
  &:focus {
    outline: none;
  }
}

.item_txt {
  flex:1;
  overflow: hidden;
  width: calc(100% - 3.5rem);
  cursor: pointer;
}

input[type="checkbox"] {
  margin: 0 20px;
  &:checked + .item_txt{
    color: gray;
    text-decoration: line-through;
  }
}

.btn_remove, .btn_ok {
  z-index: 1;
  height: 40px;
  background: $color2;
  width: 3.5rem;
  margin-left:auto;
  font-size: 1.6rem;
  border-radius: 0 6px 6px 0;
  color: $white;
  font-weight: bold;
  text-shadow: 1px 1px 1px rgb(50, 34, 0);
  cursor: pointer;
  &:hover{
    background: $color3;
  }
  &:focus {
    outline: none;
  }
}

.edit{
  width: 100%;
  border:none;
  padding: 13px;
  border-radius: 6px 0 0 6px;
}
