























@import '../scss/variable';

.header{
  overflow:hidden;
  width: 100%;
  .tit {
    height: 60px;
    color: #333;
    position: relative;
  }
  .logo{
    width: 50px;
  }
}
.btn_reset {
  float: right;
  padding:10px 20px;
  background: $point1;
  font-size: 0.9rem;
  border-radius: 6px;
  color: $white;
  cursor: pointer;
  &:hover{  
    background: $point2;
  }
  &:focus{
    outline: none;
  }
}
.num{
  padding: 60px 0 10px;
  text-align: right;
  font-size: 1rem;
  color: #333;
  > span{
    display: inline-block;
    padding:0 10px;
    color: $point2;
    opacity: 0.5;
    font-size: 0.8rem;
    vertical-align: text-top;
  }
}
